          <div class="card-body">
            <div v-if="searchHistory.length === 0" class="text-center text-muted py-3">
              <i class="bi bi-clock fa-2x mb-2 opacity-50"></i>
              <p class="mb-0">暂无搜索历史</p>
            </div>
            <div v-else>
              <div 
                v-for="(history, index) in searchHistory.slice(0, 5)" 
