                  {{ shortcut.label }}
                </button>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>

    <!-- 搜索结果 -->
    <div v-if="hasSearched" class="row">
      <div class="col-12">
        <div class="card border-0 shadow-sm">
          <div class="card-header bg-white border-0">
            <div class="d-flex justify-content-between align-items-center">
              <div>
                <h6 class="fw-bold mb-0">
                  <i class="bi bi-list-ul me-2 text-primary"></i>搜索结果
                  <span class="badge bg-primary ms-2">{{ searchResults.length }}</span>
                </h6>
                <small class="text-muted">搜索用时: {{ searchTime }}ms</small>
              </div>
              <div class="btn-group btn-group-sm">
                <button 
                  class="btn" 
                  :class="resultViewMode === 'list' ? 'btn-primary' : 'btn-outline-primary'"
                  @click="resultViewMode = 'list'"
                >
                  <i class="bi bi-list"></i>
                </button>
                <button 
                  class="btn" 
                  :class="resultViewMode === 'card' ? 'btn-primary' : 'btn-outline-primary'"
                  @click="resultViewMode = 'card'"
