                </button>
              </div>
            </div>
          </div>
          
          <div class="card-body p-0">
            <!-- 列表视图 -->
            <div v-if="resultViewMode === 'list'">
              <div v-if="searchResults.length === 0" class="text-center py-5">
                <div class="empty-state">
                  <i class="bi bi-search fa-3x mb-3 text-secondary opacity-50"></i>
