                  <p class="text-muted">请尝试调整搜索条件或使用不同的关键词</p>
                </div>
              </div>
              <div v-else>
                <div 
                  v-for="result in paginatedResults" 
                  :key="result.id"
                  class="search-result-item border-bottom p-4"
                  @click="viewRecord(result)"
                >
                  <div class="d-flex justify-content-between align-items-start">
                    <div class="flex-grow-1">
                      <div class="d-flex align-items-center mb-2">
                        <h6 class="fw-bold mb-0 me-3">{{ result.title }}</h6>
                        <span class="badge" :class="getStatusClass(result.status)">{{ getStatusText(result.status) }}</span>
                      </div>
                      <div class="text-muted mb-2">
                        <span class="me-3">
                          <i class="bi bi-file-text me-1"></i>{{ result.caseNumber }}
                        </span>
                        <span class="me-3">
                          <i class="bi bi-person me-1"></i>{{ result.recorder }}
                        </span>
                        <span>
                          <i class="bi bi-calendar me-1"></i>{{ formatDate(result.createdAt) }}
                        </span>
                      </div>
                      <div class="search-snippet" v-html="highlightKeywords(result.snippet)"></div>
                      <div class="mt-2">
                        <small class="text-muted">
                          相关度: {{ result.relevance }}% | 字数: {{ result.wordCount }}
                        </small>
                      </div>
                    </div>
                    <div class="ms-3">
                      <div class="btn-group-vertical btn-group-sm">
                        <button class="btn btn-outline-primary" @click.stop="viewRecord(result)">
                          <i class="bi bi-eye"></i>
                        </button>
                        <button class="btn btn-outline-success" @click.stop="editRecord(result)">
                          <i class="bi bi-pencil"></i>
                        </button>
                      </div>
                    </div>
                  </div>
                </div>
              </div>
            </div>

            <!-- 卡片视图 -->
            <div v-else class="row g-3 p-3">
              <div v-if="searchResults.length === 0" class="col-12">
                <div class="text-center py-5">
                  <div class="empty-state">
                    <i class="bi bi-search fa-3x mb-3 text-secondary opacity-50"></i>
                    <h5 class="text-secondary">未找到相关记录</h5>
                    <p class="text-muted">请尝试调整搜索条件或使用不同的关键词</p>
                  </div>
                </div>
              </div>
              <div v-else v-for="result in paginatedResults" :key="result.id" class="col-md-6 col-lg-4">
                <div class="card h-100 search-result-card" @click="viewRecord(result)">
                  <div class="card-header d-flex justify-content-between align-items-center">
                    <small class="text-muted">{{ result.caseNumber }}</small>
                    <span class="badge" :class="getStatusClass(result.status)">{{ getStatusText(result.status) }}</span>
                  </div>
                  <div class="card-body">
                    <h6 class="card-title">{{ result.title }}</h6>
                    <div class="search-snippet small" v-html="highlightKeywords(result.snippet)"></div>
                    <div class="mt-2">
                      <small class="text-muted d-block">
                        <i class="bi bi-person me-1"></i>{{ result.recorder }}
                      </small>
                      <small class="text-muted d-block">
                        <i class="bi bi-calendar me-1"></i>{{ formatDate(result.createdAt) }}
                      </small>
                      <small class="text-muted d-block">
                        相关度: {{ result.relevance }}%
                      </small>
                    </div>
                  </div>
                  <div class="card-footer bg-transparent">
                    <div class="btn-group w-100">
                      <button class="btn btn-outline-primary btn-sm" @click.stop="viewRecord(result)">
                        <i class="bi bi-eye me-1"></i>查看
                      </button>
                      <button class="btn btn-outline-success btn-sm" @click.stop="editRecord(result)">
                        <i class="bi bi-pencil me-1"></i>编辑
                      </button>
                    </div>
                  </div>
                </div>
              </div>
            </div>
          </div>
          
          <!-- 分页 -->
          <div v-if="searchResults.length > 0" class="card-footer bg-white">
            <div class="d-flex justify-content-between align-items-center">
              <div class="text-muted">
                显示 {{ (currentPage - 1) * pageSize + 1 }} - {{ Math.min(currentPage * pageSize, searchResults.length) }} 条，共 {{ searchResults.length }} 条结果
              </div>
              <nav>
                <ul class="pagination pagination-sm mb-0">
                  <li class="page-item" :class="{ disabled: currentPage === 1 }">
                    <button class="page-link" @click="currentPage = 1" :disabled="currentPage === 1">
                      <i class="bi bi-chevron-double-left"></i>
                    </button>
                  </li>
                  <li class="page-item" :class="{ disabled: currentPage === 1 }">
                    <button class="page-link" @click="currentPage--" :disabled="currentPage === 1">
                      <i class="bi bi-chevron-left"></i>
                    </button>
                  </li>
                  <li 
                    v-for="page in visiblePages" 
                    :key="page" 
                    class="page-item" 
                    :class="{ active: page === currentPage }"
                  >
                    <button class="page-link" @click="currentPage = page">{{ page }}</button>
                  </li>
                  <li class="page-item" :class="{ disabled: currentPage === totalPages }">
                    <button class="page-link" @click="currentPage++" :disabled="currentPage === totalPages">
                      <i class="bi bi-chevron-right"></i>
                    </button>
                  </li>
                  <li class="page-item" :class="{ disabled: currentPage === totalPages }">
                    <button class="page-link" @click="currentPage = totalPages" :disabled="currentPage === totalPages">
                      <i class="bi bi-chevron-double-right"></i>
                    </button>
                  </li>
                </ul>
              </nav>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: 'SearchPage',
  data() {
    return {
      showAdvancedSearch: false,
      hasSearched: false,
      resultViewMode: 'list',
      currentPage: 1,
      pageSize: 10,
      searchTime: 0,
      searchForm: {
        keyword: '',
        searchType: 'all',
        sortBy: 'relevance',
        caseType: '',
        recorder: '',
        dateFrom: '',
        dateTo: '',
        status: '',
        location: '',
        minWords: '',
        maxWords: ''
      },
      searchResults: [],
      searchHistory: [
        {
          keyword: '张某盗窃',
          timestamp: '2024-01-17 10:30',
          resultCount: 3
        },
        {
          keyword: '交通肇事',
          timestamp: '2024-01-16 15:20',
          resultCount: 5
        },
        {
          keyword: '诈骗案',
          timestamp: '2024-01-15 09:15',
          resultCount: 2
        }
      ],
      quickSearches: [
        { label: '今日笔录', icon: 'bi bi-calendar-day', query: { dateFrom: new Date().toISOString().split('T')[0] } },
        { label: '草稿状态', icon: 'bi bi-file-earmark-text', query: { status: 'draft' } },
        { label: '刑事案件', icon: 'bi bi-shield-exclamation', query: { caseType: 'criminal' } },
        { label: '已审核', icon: 'bi bi-check-circle', query: { status: 'verified' } },
        { label: '本周记录', icon: 'bi bi-calendar-week', query: { dateFrom: this.getWeekStart() } },
        { label: '长笔录', icon: 'bi bi-file-text', query: { minWords: 1000 } }
      ],
      // 模拟搜索结果数据
      mockResults: [
        {
          id: 1,
          caseNumber: 'CASE-2024-001',
          title: '张某涉嫌盗窃案询问笔录',
          snippet: '犯罪嫌疑人张某，男，25岁，无业。于2024年1月10日在某商场实施盗窃行为，被现场抓获。经询问，张某对其盗窃行为供认不讳...',
          recorder: '李警官',
          createdAt: '2024-01-15 14:30:00',
          status: 'completed',
          relevance: 95,
          wordCount: 1250
        },
        {
          id: 2,
          caseNumber: 'CASE-2024-002',
          title: '王某交通肇事案询问笔录',
          snippet: '当事人王某，男，35岁，司机。于2024年1月12日驾驶车辆在某路段发生交通事故，造成一人轻伤。经询问了解事故经过...',
          recorder: '张警官',
          createdAt: '2024-01-16 09:15:00',
          status: 'verified',
          relevance: 88,
          wordCount: 980
        },
        {
          id: 3,
          caseNumber: 'CASE-2024-003',
          title: '刘某诈骗案询问笔录',
          snippet: '犯罪嫌疑人刘某，女，28岁，无固定职业。涉嫌通过网络平台实施诈骗，涉案金额约5万元。经询问，刘某承认部分犯罪事实...',
          recorder: '王警官',
          createdAt: '2024-01-17 16:45:00',
          status: 'draft',
          relevance: 92,
          wordCount: 1580
        }
      ]
    }
  },
  computed: {
    totalPages() {
      return Math.ceil(this.searchResults.length / this.pageSize);
    },
    paginatedResults() {
      const start = (this.currentPage - 1) * this.pageSize;
      const end = start + this.pageSize;
      return this.searchResults.slice(start, end);
    },
    visiblePages() {
      const pages = [];
      const total = this.totalPages;
      const current = this.currentPage;
      
      if (total <= 7) {
        for (let i = 1; i <= total; i++) {
          pages.push(i);
        }
      } else {
        if (current <= 4) {
          for (let i = 1; i <= 5; i++) {
            pages.push(i);
          }
          pages.push('...');
          pages.push(total);
        } else if (current >= total - 3) {
          pages.push(1);
          pages.push('...');
          for (let i = total - 4; i <= total; i++) {
            pages.push(i);
          }
        } else {
          pages.push(1);
          pages.push('...');
          for (let i = current - 1; i <= current + 1; i++) {
            pages.push(i);
          }
          pages.push('...');
          pages.push(total);
        }
      }
      
      return pages;
    }
  },
  methods: {
    performSearch() {
      if (!this.searchForm.keyword.trim() && !this.hasAdvancedFilters()) {
        alert('请输入搜索关键词或设置搜索条件');
        return;
      }
      
      const startTime = Date.now();
      
      // 模拟搜索过程
      setTimeout(() => {
        this.searchTime = Date.now() - startTime;
        this.searchResults = this.mockResults.filter(result => {
          return this.matchesSearchCriteria(result);
        });
        
        // 排序结果
        this.sortResults();
        
        this.hasSearched = true;
        this.currentPage = 1;
        
        // 添加到搜索历史
        if (this.searchForm.keyword.trim()) {
          this.addToSearchHistory();
        }
      }, 200);
    },
    
    matchesSearchCriteria(result) {
      const keyword = this.searchForm.keyword.toLowerCase();
      
      // 关键词匹配
      if (keyword) {
        const searchIn = this.searchForm.searchType === 'all' ? 
          `${result.title} ${result.snippet} ${result.caseNumber}`.toLowerCase() :
          this.getSearchField(result, this.searchForm.searchType).toLowerCase();
        
        if (!searchIn.includes(keyword)) {
          return false;
        }
      }
      
      // 其他筛选条件
      if (this.searchForm.status && result.status !== this.searchForm.status) {
        return false;
      }
      
      if (this.searchForm.recorder && !result.recorder.includes(this.searchForm.recorder)) {
        return false;
      }
      
      if (this.searchForm.dateFrom && result.createdAt < this.searchForm.dateFrom) {
        return false;
      }
      
      if (this.searchForm.dateTo && result.createdAt > this.searchForm.dateTo) {
        return false;
      }
      
      if (this.searchForm.minWords && result.wordCount < parseInt(this.searchForm.minWords)) {
        return false;
      }
      
      if (this.searchForm.maxWords && result.wordCount > parseInt(this.searchForm.maxWords)) {
        return false;
      }
      
      return true;
    },
    
    getSearchField(result, searchType) {
      switch (searchType) {
        case 'title': return result.title;
        case 'content': return result.snippet;
        case 'case': return result.caseNumber;
        case 'person': return result.recorder;
        default: return `${result.title} ${result.snippet} ${result.caseNumber}`;
      }
    },
    
    sortResults() {
      switch (this.searchForm.sortBy) {
        case 'relevance':
          this.searchResults.sort((a, b) => b.relevance - a.relevance);
          break;
        case 'date_desc':
          this.searchResults.sort((a, b) => new Date(b.createdAt) - new Date(a.createdAt));
          break;
        case 'date_asc':
          this.searchResults.sort((a, b) => new Date(a.createdAt) - new Date(b.createdAt));
          break;
        case 'case_number':
          this.searchResults.sort((a, b) => a.caseNumber.localeCompare(b.caseNumber));
          break;
      }
    },
    
    hasAdvancedFilters() {
      return this.searchForm.caseType || this.searchForm.recorder || 
             this.searchForm.dateFrom || this.searchForm.dateTo || 
             this.searchForm.status || this.searchForm.location ||
             this.searchForm.minWords || this.searchForm.maxWords;
    },
    
    addToSearchHistory() {
      const historyItem = {
        keyword: this.searchForm.keyword,
        timestamp: new Date().toLocaleString('zh-CN'),
        resultCount: this.searchResults.length
      };
      
      // 移除重复项
      this.searchHistory = this.searchHistory.filter(h => h.keyword !== historyItem.keyword);
      // 添加到开头
      this.searchHistory.unshift(historyItem);
      // 保持最多10条历史记录
      this.searchHistory = this.searchHistory.slice(0, 10);
    },
    
    loadSearchHistory(history) {
      this.searchForm.keyword = history.keyword;
      this.performSearch();
    },
    
    performQuickSearch(shortcut) {
      // 重置搜索表单
      this.resetSearchForm();
      // 应用快捷搜索条件
      Object.assign(this.searchForm, shortcut.query);
      this.performSearch();
    },
    
    resetSearchForm() {
      this.searchForm = {
        keyword: '',
        searchType: 'all',
        sortBy: 'relevance',
        caseType: '',
        recorder: '',
        dateFrom: '',
        dateTo: '',
        status: '',
        location: '',
        minWords: '',
        maxWords: ''
      };
    },
    
    saveSearchTemplate() {
      // 这里可以实现保存搜索模板的功能
      console.log('保存搜索模板:', this.searchForm);
      alert('搜索模板已保存');
    },
    
    exportResults() {
      // 这里可以实现导出搜索结果的功能
      console.log('导出搜索结果:', this.searchResults);
      alert('搜索结果导出功能开发中');
    },
    
    highlightKeywords(text) {
      if (!this.searchForm.keyword.trim()) {
        return text;
      }
      
      const keyword = this.searchForm.keyword.trim();
      const regex = new RegExp(`(${keyword})`, 'gi');
      return text.replace(regex, '<mark class="bg-warning">$1</mark>');
    },
    
    viewRecord(record) {
      console.log('查看笔录:', record);
      // 这里可以跳转到详情页面
    },
    
    editRecord(record) {
      console.log('编辑笔录:', record);
      // 这里可以跳转到编辑页面
    },
    
    getStatusClass(status) {
      const classes = {
        draft: 'bg-warning',
        completed: 'bg-success',
        verified: 'bg-primary'
      };
      return classes[status] || 'bg-secondary';
    },
    
    getStatusText(status) {
      const texts = {
        draft: '草稿',
        completed: '已完成',
        verified: '已审核'
      };
      return texts[status] || '未知';
    },
    
    formatDate(dateStr) {
      return new Date(dateStr).toLocaleString('zh-CN');
    },
    
    getWeekStart() {
      const now = new Date();
      const dayOfWeek = now.getDay();
      const diff = now.getDate() - dayOfWeek + (dayOfWeek === 0 ? -6 : 1);
      return new Date(now.setDate(diff)).toISOString().split('T')[0];
    }
  },
  
  mounted() {
    console.log('搜索页面已加载');
  }
}
</script>

