<script>
export default {
  name: 'CaseManagement',
  data() {
    return {
      viewMode: 'table',
      currentPage: 1,
      pageSize: 10,
      showCreateModal: false,
      showImportModal: false,
      selectedCases: [],
      searchForm: {
        keyword: '',
        caseType: '',
        status: '',
        officer: '',
        dateFrom: ''
      },
      caseForm: {
        caseNumber: '',
        title: '',
        description: '',
        caseType: '',
        status: 'pending',
        officer: '',
        location: '',
        incidentTime: ''
      },
      stats: {
        totalCases: 25,
        pendingCases: 8,
        completedCases: 15,
        todayCases: 3
      },
      cases: [
        {
          id: 1,
          caseNumber: 'CASE-2024-001',
          title: '张某涉嫌盗窃案',
          description: '犯罪嫌疑人张某在某商场实施盗窃，被现场抓获',
          caseType: 'criminal',
          status: 'investigating',
          officer: '李警官',
          location: '某商场',
          incidentTime: '2024-01-10 15:30:00',
          createdAt: '2024-01-15 14:30:00',
          recordCount: 3,
          daysOpen: 7
        },
        {
          id: 2,
          caseNumber: 'CASE-2024-002',
          title: '王某交通肇事案',
          description: '王某驾驶车辆发生交通事故，造成一人轻伤',
          caseType: 'traffic',
          status: 'completed',
          officer: '张警官',
          location: '某路段',
          incidentTime: '2024-01-12 08:45:00',
          createdAt: '2024-01-16 09:15:00',
          recordCount: 2,
          daysOpen: 5
        },
        {
          id: 3,
          caseNumber: 'CASE-2024-003',
          title: '刘某诈骗案',
          description: '刘某通过网络平台实施诈骗，涉案金额约5万元',
          caseType: 'criminal',
          status: 'pending',
          officer: '王警官',
          location: '网络',
          incidentTime: '2024-01-14 20:00:00',
          createdAt: '2024-01-17 16:45:00',
          recordCount: 1,
          daysOpen: 3
        },
        {
          id: 4,
          caseNumber: 'CASE-2024-004',
          title: '李某合同纠纷案',
          description: '李某与某公司发生合同纠纷，要求赔偿损失',
          caseType: 'civil',
          status: 'investigating',
          officer: '李警官',
          location: '某公司',
          incidentTime: '2024-01-08 14:20:00',
          createdAt: '2024-01-18 10:30:00',
          recordCount: 4,
          daysOpen: 10
        }
      ]
    }
  },
  computed: {
    filteredCases() {
      let filtered = this.cases;
      
      if (this.searchForm.keyword) {
        const keyword = this.searchForm.keyword.toLowerCase();
        filtered = filtered.filter(caseItem => 
          caseItem.title.toLowerCase().includes(keyword) ||
          caseItem.caseNumber.toLowerCase().includes(keyword) ||
          caseItem.description.toLowerCase().includes(keyword)
        );
      }
      
      if (this.searchForm.caseType) {
        filtered = filtered.filter(caseItem => caseItem.caseType === this.searchForm.caseType);
      }
      
      if (this.searchForm.status) {
        filtered = filtered.filter(caseItem => caseItem.status === this.searchForm.status);
      }
      
      if (this.searchForm.officer) {
        filtered = filtered.filter(caseItem => caseItem.officer === this.searchForm.officer);
      }
      
      if (this.searchForm.dateFrom) {
        filtered = filtered.filter(caseItem => caseItem.createdAt >= this.searchForm.dateFrom);
      }
      
      return filtered;
    },
    totalPages() {
      return Math.ceil(this.filteredCases.length / this.pageSize);
    },
    paginatedCases() {
      const start = (this.currentPage - 1) * this.pageSize;
      const end = start + this.pageSize;
      return this.filteredCases.slice(start, end);
    },
    visiblePages() {
      const pages = [];
      const total = this.totalPages;
      const current = this.currentPage;
      
      if (total <= 7) {
        for (let i = 1; i <= total; i++) {
          pages.push(i);
        }
      } else {
        if (current <= 4) {
          for (let i = 1; i <= 5; i++) {
            pages.push(i);
          }
          pages.push('...');
          pages.push(total);
        } else if (current >= total - 3) {
          pages.push(1);
          pages.push('...');
          for (let i = total - 4; i <= total; i++) {
            pages.push(i);
          }
        } else {
          pages.push(1);
          pages.push('...');
          for (let i = current - 1; i <= current + 1; i++) {
            pages.push(i);
          }
          pages.push('...');
          pages.push(total);
        }
      }
      
      return pages;
    }
  },
  methods: {
    searchCases() {
      this.currentPage = 1;
      console.log('搜索案件:', this.searchForm);
    },
    createCase() {
      if (!this.caseForm.caseNumber || !this.caseForm.title || !this.caseForm.caseType) {
        alert('请填写完整信息');
        return;
      }
      
      const newCase = {
        id: Date.now(),
        ...this.caseForm,
        createdAt: new Date().toISOString().slice(0, 19).replace('T', ' '),
        recordCount: 0,
        daysOpen: 0
      };
      
      this.cases.unshift(newCase);
      this.showCreateModal = false;
      this.caseForm = {
        caseNumber: '',
        title: '',
        description: '',
        caseType: '',
        status: 'pending',
        officer: '',
        location: '',
        incidentTime: ''
      };
      
      // 更新统计数据
      this.updateStats();
    },
    viewCase(caseItem) {
      console.log('查看案件详情:', caseItem);
      // 这里可以跳转到案件详情页面
    },
    editCase(caseItem) {
      console.log('编辑案件:', caseItem);
      // 这里可以打开编辑模态框或跳转到编辑页面
    },
    viewRecords(caseItem) {
      console.log('查看案件笔录:', caseItem);
      // 这里可以跳转到笔录列表页面，并筛选该案件的笔录
      this.$router.push({ name: 'TaskList', query: { caseNumber: caseItem.caseNumber } });
    },
    deleteCase(id) {
      if (confirm('确定要删除这个案件吗？删除后将无法恢复！')) {
        const index = this.cases.findIndex(c => c.id === id);
        if (index > -1) {
          this.cases.splice(index, 1);
          this.updateStats();
        }
      }
    },
    toggleSelectAll(event) {
      if (event.target.checked) {
        this.selectedCases = this.paginatedCases.map(c => c.id);
      } else {
        this.selectedCases = [];
      }
    },
    exportCases() {
      console.log('导出案件数据');
      alert('案件导出功能开发中');
    },
    updateStats() {
      this.stats.totalCases = this.cases.length;
      this.stats.pendingCases = this.cases.filter(c => c.status === 'pending').length;
      this.stats.completedCases = this.cases.filter(c => c.status === 'completed').length;
      
      const today = new Date().toISOString().split('T')[0];
      this.stats.todayCases = this.cases.filter(c => c.createdAt.startsWith(today)).length;
    },
    getCaseTypeClass(caseType) {
      const classes = {
        criminal: 'bg-danger',
        civil: 'bg-primary',
        administrative: 'bg-warning',
        traffic: 'bg-info'
      };
      return classes[caseType] || 'bg-secondary';
    },
    getCaseTypeText(caseType) {
      const texts = {
        criminal: '刑事',
        civil: '民事',
        administrative: '行政',
        traffic: '交通'
      };
      return texts[caseType] || '未知';
    },
    getStatusClass(status) {
      const classes = {
        pending: 'bg-warning',
        investigating: 'bg-info',
        completed: 'bg-success',
        closed: 'bg-secondary'
      };
      return classes[status] || 'bg-secondary';
    },
    getStatusText(status) {
      const texts = {
        pending: '待处理',
        investigating: '调查中',
        completed: '已完成',
        closed: '已结案'
      };
      return texts[status] || '未知';
    },
    formatDate(dateStr) {
      return new Date(dateStr).toLocaleString('zh-CN');
    }
  },
  mounted() {
    console.log('案件管理页面已加载');
    this.updateStats();
  }
}
</script>

